import React, {FC} from 'react'
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material'
import { IModalFavAlert } from '../interface/IModalFavAlert'

export const ModalFavAlert:FC<IModalFavAlert> = (props) => {
  const {open, setOpen, onClick} = props
  return (
    <Dialog
      open={open}
      onClose={setOpen}
    >
      <DialogTitle>
           
      </DialogTitle>
      <DialogContent>
        <DialogContentText>
              ?     
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button variant='outlined' onClick={setOpen}></Button>
        <Button variant='contained' color='error' onClick={onClick}></Button>
      </DialogActions>
    </Dialog>
  )
}